/* Define Variables */

$primaryfont: "Work Sans", sans-serif;
$secondaryfont: "Indie Flower", serif;

$accentcolor: #8686b8;
$accentcolor2: #b6b6ce;
$beigecolor: #f3f3f2;
$bluecolor: #bddfe8;
$greycolor: #f3f3f2;

$box-shadow: 0 0 21px #b2b2c3;

/* Mixin */

@mixin flex-center {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
}

@mixin transform($property) {
    -webkit-transform: $property;
    -ms-transform: $property;
    transform: $property;
  }

@mixin whiteborder {
     box-shadow: $box-shadow;
     border-radius: 4px;
     border: #ffffff 45px solid;
  }

/* CSS reset */

html, body, p, h1, h2, h3 {
    margin: 0px;
    padding: 0px;
    font-weight: normal;
    font-weight: 300;
}

ul {
    margin: 0;
    list-style-type: none;
    padding-left: 0px;
    text-align: left;
}

/* Element styles */

body {
    background-color: $greycolor;
    font-family: $primaryfont;
    letter-spacing: 1.0px;
    // background-image: url("./whitemap.png");
    background-image: url("./world.svg");  
    background-position: top center;
   }

* { box-sizing: border-box; }

h1 {
    color: $greycolor;
    font-size: 16px;
    letter-spacing: 5px;
    text-transform: uppercase;
    text-align: center;
    line-height: 2.0;

     i {
        padding-right: 20px;
    }

}

button { cursor: pointer; }

h2 { 
    text-transform: uppercase;
    
    i {
        padding-right: 20px;
    }

}


/* Header and Footer */

header, footer {
    @include flex-center;
    padding: 30px;
    background: $accentcolor;
    flex-direction: column;
    gap: 12px;
}

header { 
    position: fixed;
    z-index: 9;
    width: 100%;

    nav button {
    border: none;
    padding: 10px 20px;
    letter-spacing: 3px;
    font-size: 12px;
    text-transform: uppercase;
    font-family: $primaryfont;
    font-weight: 100;
    background: $greycolor;
    cursor: pointer;
    margin-right: 20px;
     }


    .filter-button {
        padding: 12px 20px;
        background: $greycolor;
        color: #8686b8;
        letter-spacing: 2px;
        font-size: 11px;
        font-family: "Work Sans";
        border: none;
        border-radius: 2px;
        margin-left: 10px;
        text-transform: uppercase;
    } 

    .filter-button:hover {
        opacity: 80%;
        background: $greycolor;
        color: #8686b8;
    }

}

nav button:last-child {
    margin-right: 0px;
    }

footer {
    font-size: 12px;
    color: $greycolor;
    margin-top: 150px;
}

main, .banner, .container {
    margin: 0 auto;
    max-width: 1150px;
}

main {
    margin: 50px auto 0px auto;
}

/* Breadcrumb */

.timeline-section.bread-search {
    border: #ffffff 25px solid;
    flex-direction: row;
    justify-content: space-between;
    border-left-width: 45px;
    border-right-width: 45px;
}

.breadcrumb .filter-title {
    justify-content: left;
    font-family: 'Work Sans';
    font-size: 14px;
    letter-spacing: 4px;
}

/* Search */

.search { 
    position: relative;

    input {
        font-family: "Work Sans";
        background: $greycolor;
        padding: 14px 20px 14px 40px;
        font-size: 11px;
        border: none;
        letter-spacing: 1.5px;
        display: inline;
        width: 100%;
        text-transform: uppercase;
    }

    i {
        position: absolute;
        top: 12px;
        left: 12px;
        color: $accentcolor;
        display: inline-block;
    }

    .input-hidden-label {
        height: 0;
        visibility: hidden;
        padding: 0px 20px;
    }
}

.no-results {
    font-size: 18px;
    margin: 45px 20px 0px 20px;
}

/* Card */

.cards-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    gap: 48px;
}

.card {
    display: flex;
    padding: 20px 0px;
    max-width: 929px;
    position: relative;
}

.card-left {
    position: relative;
    border: none;
    padding: 0;
    cursor: pointer;
    @include flex-center;

    .travel_img {
        max-width: 350px;
        height: 515px;
        @include whiteborder;
        border-bottom: 80px #ffffff solid;
      }
}


.card .card-right {
        display: none; 
        padding-left: 20px;
        line-height: 2.0;
        width: 100%;
        border-radius: 4px;

        > div {
            margin: 5px 0px;
        }
    }

.card.card-open .card-right {
    @include flex-center;
    flex-direction: column;
    position: absolute;
    background: #ffffff;
    padding: 0px 36px;
    height: 93%;
    gap: 20px;
    cursor: pointer;
 }


.travel_locationData, .travel_dates {
    @include flex-center;
    gap: 10px;
}

.travel_locationData {
    flex-direction: column;
}

.travel_dates {
    text-transform: uppercase;
    font-size: 12px;
}

.travel_location {
    text-transform: uppercase;
    font-size: 14px;
    margin-left: 7px;
}

span.travel_description {
    text-align: center;
}

.travel_locationData i, .travel_locationData a {
    color: $accentcolor;
}

.travel_locationData a {
    text-decoration: none;
}

.travel_title {
    font-size: 25px;
    text-transform: capitalize;
    font-size: 1.5em;
}

.travel_title2 {
    font-family: $secondaryfont;
    letter-spacing: 5px;
    position: absolute;
    bottom: 25px;
    font-size: 1.5em;
}


/* Number */

.number {
    font-weight: bold;
    font-size: 30px;
    letter-spacing: 6px;
    color: $accentcolor;
}

.timeline-num {
    @include flex-center;
    font-size: 14px;
    margin: 0 20px;
}

.number-container {
display: flex;
gap: 15px;
justify-content: space-between;
flex-wrap: wrap;
}

/* Header */

header.fixed-header {
    position: fixed;
    z-index: 99999;
    justify-content: space-between;
    width: 100%;
    padding: 15px;
} 

header.fixed-header .container {
    display: flex;
    justify-content: space-between;
    width: 100%;
    align-items: center;
}

.header-filter { display: none; }

header.fixed-header .header-filter { 
    color: #ffffff; 
    display: block; 
    text-transform: uppercase;
    letter-spacing: 2px;
    font-size: 12px; 
}


/* Banner */

.banner {
    display: flex;
    gap: 20px;
    justify-content: space-around;
    align-items: center;
    padding-top: 150px;

    > div {
        @include flex-center;
        text-align: center;
    }
}

.polaroid-banner {
    position: relative;

    img {
        max-width: 380px;
        @include whiteborder;
        border-bottom: 80px white solid;
    }

}

.social-icons-row a {
    font-size: 100px;
    margin: auto 30px;
    color: $accentcolor;
    cursor: pointer;
}


.ninety-days-banner {

    position: relative; 

    .ninety-days-banner-rotate {
    position: absolute;
    color: $greycolor;
    @include transform(rotate(-12deg));
    line-height: 3;
    letter-spacing: 6px;
    max-width: 300px;
    font-size: 12px;
    font-family: $secondaryfont;
    }

    img {
        max-width: 440px;
        position: relative;
    }
}

.bucketlist .ninety-days-banner {

    img {
        max-width: 100%;
        height: auto;
        transform: rotate(8.5deg);
    }

    ul {
        list-style-type: none;
        padding-left: 0;
    }

    li:before {
        content: '\f0c8';
        font-family: "Font Awesome 5 Free";
        margin-right: 10px;
        font-weight: 400;
    }

    li.checked:before {
        content: '\f14a';
        margin-right: 10px;
        font-weight: 400;
    }

    li {
        font-size: 18px;
        text-align: left;
    }

    .ninety-days-banner-rotate {
        max-width: 100%;
        font-size: 20px;
        transform: rotate(0deg);
        top: 15%;
    }
}

/* Sidebyside components */

.compflex {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;

    .timeline-section {
        margin: 20px 0px;
    }
}

.compflex.twothirds { 
    > div:first-child {
        width: 38%;
    }

    > div:last-child {
        width: 60%;
    }
}



/* Map Filter */

img.button-map { max-width: 100%; }


.map-buttons-container {
    overflow: hidden;
    background: #ffffff;
}

.button-map-section {
    @include whiteborder;
    background-color: $greycolor;
    width: 100%;
    height: auto;
    border-top: white 100px solid;
    position: relative;
    text-align: center;

    button {
        position: absolute;
        border: none;
        color: $greycolor;
        background: $accentcolor;
        padding: 10px 15px;
        letter-spacing: 3px;
        font-size: 11px;
        text-transform: uppercase;
        font-family: $primaryfont;
        font-weight: 100;
        cursor: pointer;
    }

    button:focus {
        outline: 4px solid white;
        box-shadow: $box-shadow;
    }
}

.hidden { 
    display: none; 
}

.map-buttons .active .hidden {
    display: flex;
    position: absolute;
    font-size: 11px;
    font-weight: 400;
    margin-left: 25px;
    background: $greycolor;
    padding: 5px 8px;
    color: $accentcolor;
    border-top-right-radius: 4px;
    border-bottom-right-radius: 4px;
    box-shadow: 2px 2px 2px #b2b2c3;
    width: max-content;
    font-family: "Indie Flower", cursive; 
}

.map-buttons .active .hidden:before {
    content: "";
    position: absolute;
    left: -12.6px;
    top: 0px;
    width: 0px;
    height: 0px;
    border-top: 13px solid transparent;
    border-bottom: 13px solid transparent;
    border-right: 13px solid $greycolor;
}

.map-buttons .fi { margin-left: 5px;}

button.deactivate, .deactivate button {
    opacity: 55%;
    pointer-events: none;
}

.timeline-section button.deactivate {
    opacity: 40%;
}

button.active.deactivate {
    box-shadow: 0 0 21px #000000; 
}


/* Zoom Buttons */

.map-buttons {
    position: relative;
    padding: 10px;
    background-color: $bluecolor;
    transform: scale(1.0);
    transform-origin: top;

    svg {
        width: 100%;
        height: 100%;
        position: absolute;
        left: 0;
        right: 0;
    }
}

.zoom-directions {
    position: absolute;
    display: flex;
    top: 56px;

    button {
        position: relative;
        min-width: 44px;
    }
} 

.zoom-controls {
    position: relative;
    z-index: 9;

    button {
        border-radius: 2px;
        box-shadow: 0px 2px 3px #b2b2c3;
    }
}


button.zoom-btn-up {
    left: 66px;
    top: -1px;
}

button.zoom-btn-down {
    bottom: -75px;
    left: 14px;
}

button.zoom-btn-center {
    top: 37px;
    left: -38px;
}

button.zoom-btn-left {
    top: 37px;
    left: -146px;
}

button.zoom-btn-right {
    top: 37px;
    left: -89px;
}

.zoom-controls > button {
    min-width: 163px;
    top: 10px;
    left: 10px;
}

/* Zoom directions */ 

.zoom-on {
    transform: scale(1.8);
}

.map-buttons.zoom-on.zoom-up.zoom-center {
    transform-origin: top;
}

.map-buttons.zoom-on.zoom-down.zoom-center {
    transform-origin: bottom;
}

.map-buttons.zoom-on.zoom-left.zoom-center {
    transform-origin: left;
}

.map-buttons.zoom-on.zoom-right.zoom-center {
    transform-origin: right;
}

.map-buttons.zoom-on.zoom-center {
    transform-origin: center;
}

.map-buttons.zoom-on.zoom-up.zoom-left.zoom-center {
    transform-origin: top left;
}

.map-buttons.zoom-on.zoom-up.zoom-right.zoom-center {
    transform-origin: top right;
}

.map-buttons.zoom-on.zoom-down.zoom-left.zoom-center {
    transform-origin: bottom left;
}

.map-buttons.zoom-on.zoom-down.zoom-right.zoom-center {
    transform-origin: bottom right;
}

