    }
}


.button-map-section button {
    padding: 12px 20px;
    background: $greycolor;
    color: #8686b8;
    font-weight: 400;
}

.button-map-section button.All {
    top: 2px;
    right: 2px; 
}

h2.filter-title {
    position: absolute;
    width: 100%;
    @include flex-center;
    top: -50px;
    letter-spacing: 5px;
    font-size: 16px;
}

/* Year */

.year-buttons { 
    display: flex; 
    align-items: center; 
    justify-content: center;
    position: relative;
    pointer-events: none;

    select {
        font-family: "Work Sans";
        letter-spacing: 1.5px;
        font-weight: 100;
        cursor: pointer;
        background: $greycolor;
        border: none;
        padding: 14px 45px 14px 45px;
        font-size: 13px;
        text-transform: uppercase;
        appearance: none;
        pointer-events: all;
    }
}

.year-buttons:before {
    font-family: "Font Awesome 6 Free"; 
    content: "\f073";
    font-size: 20px;
    color: $accentcolor;
    margin-bottom: 2px;
    position: absolute;
    left: 12px;
  }

.year-buttons:after {
    font-size: 20px;
    color: $accentcolor;
    margin-bottom: 2px;
    position: absolute;
    right: 12px;
    font-family: "FontAwesome"; 
    content: "\f078";
  }

/* Timeline section */

.timeline-section {
    @include flex-center;
    flex-direction: column;
    gap: 20px;
    width: 100%;
    background: #ffffff;
    @include whiteborder;
    margin: 20px auto;

    .filter-title { 
        position: static; 
    }

    button {
        border: none;
        padding: 10px 20px;
        letter-spacing: 3px;
        font-size: 12px;
        text-transform: uppercase;
        font-family: $primaryfont;
        font-weight: 100;
        color: $greycolor; 
        background: $accentcolor;
        cursor: pointer;
        border-right: 4px solid #ffffff;
    }

    button:last-child { 
        border-right: none; 
    }
}


/* Button focus styles */

button.active, button:focus {
    outline: 4px solid white;
    border-right: none;
    z-index: 999;
    position: relative;
    box-shadow: 0 0 21px #363646;
   }

   .button-map-section button.active,
   .button-map-section  button:focus {
    position: absolute;
}

.map-buttons-container button.active:not(.All),
.map-buttons-container button:focus:not(.All) {
    color: $greycolor;
    outline: none;
    box-shadow: none;
}

.map-buttons button:hover {
    opacity: 100%;
}

.map-buttons .fa-house {
    font-size: 14px;
}

.map-buttons .fa-person-walking-luggage {
    font-size: 19px;
}

.zoom-directions button:focus {
    position: relative;
}

.button-map-section button.active.All {  
    box-shadow: 0 0 21px $accentcolor;
    top: 10px;
    right: 10px;
} 

button:hover {
    opacity: 35%;
    background: $accentcolor;
    color: $greycolor;
}

.timeline-section button:hover  { opacity: 75%; }

/* Bucket */ 

.bucketlistslogan h2 { 
    line-height: 2;
    font-size: 35px;
    letter-spacing: 5px;
    font-family: 'Indie Flower';
}

.bucketlist .polaroid-banner img { 
    max-width: 400px; 
}

.suitcase {
    text-align: center;
    padding-bottom: 20px;
    font-size: 50px;
}


/* Quote */

.quote  {
    blockquote {
        font-size: 40px;
        line-height: 2.0;
        padding: 0px 60px;
        margin: 0px;
        letter-spacing: 5px;
        font-family: 'Indie Flower';
    } 

    small {
        font-size: 25px;
    }
}


/* Comingsoon */

.coming-soon ul li { 
    font-family: 'Indie Flower'; 
    font-size: 25px;

}

/* Tags */

.tags span {
    background: $accentcolor;
    -webkit-border-bottom-right-radius: 2px;
    border-bottom-right-radius: 2px;
    -webkit-border-top-right-radius: 2px;
    border-top-right-radius: 2px;
    color: #fff;
    display: inline-block;
    font-size: .7rem;
    height: 28px;
    letter-spacing: 1px;
    line-height: 28px;
    margin: 0 24px 8px 0;
    padding: 0 12px 0 14px;
    position: relative;
}

.tags span:before {
    border-color: transparent $accentcolor transparent transparent;
    border-style: solid;
    border-width: 14px 14px 14px 0;
    content: "";
    height: 0;
    left: -13.5px;
    position: absolute;
    top: 0;
    width: 0;
}

/* Mobile Styles */

@media screen and (max-width: 1150px) {
    .cards-container { 
        justify-content: space-evenly; 
    }
    .banner { 
        flex-wrap: wrap; 
    }
}

@media screen and (max-width: 768px) {

    header.fixed-header  {
        .header-filter {
            display: flex;
            gap: 20px;
            flex-direction: column;
        }
        
        .container {
            flex-direction: column;
            gap: 10px;
        }
    }

    .zoom-on {
        transform: none;
    }

    .map-buttons.zoom-on.zoom-up.zoom-left.zoom-center {
        transform: none;
    }

    .map-buttons { 
        height: auto; 
        background: transparent;

        .fa-map-pin {
            display: none;
        }
        .fa-house {
            display: none;
        }
    }

    .button-map-section {
        background: white;
        border: #ffffff 45px solid;
        box-shadow: 0 0 21px #b2b2c3;

        .filter-title {
            position: static;
        }

        .map-buttons button {
            border: 1px solid;
            border-radius: 4px;
            padding: 4px 8px;
        }

        button { 
            margin: 6px; 
        }
    }

    img.button-map {
        border: white solid 20px;
        box-shadow: none;
    }

    .zoom-controls {
        display: none;
    }

    .button-map-section, .timeline-section {
        max-width: 90%;
        margin: 20px auto;

        button, button.active, button:focus {
            position: static;
        }

       button:not(.All) { display: inline ;}

       span.hidden {
        display: block;
        font-size: 14px;
      }

      .active .hidden { position: static; font-size: 14px; padding: 0px; margin-left: 0px; }
    }

    .map-buttons-container button.active:not(.All), 
    .map-buttons-container button:focus:not(.All) { 
        background: $accentcolor2; 
    } 

    .timeline-section .timeline-buttons {
        @include flex-center;
        flex-wrap: wrap;
        gap: 10px;

        button:last-child { 
            border-right: 4px solid #8686b7; 
        }
    }

    .bucketlist .ninety-days-banner img, .polaroid-banner img {
        max-width: 100%;
        height: auto;
    }

    .polaroid-banner {  
        max-width: 90%; 
    }

    .social-icons-row a { 
        font-size: 60px;
    }

    .bucketlist {
        .ninety-days-banner li { 
            font-size: 14px; 
        }

        .ninety-days-banner .ninety-days-banner-rotate {
            max-width: 80%;
            position: static;
            color: #000000;
        }

        .ninety-days-banner img { 
            display: none; 
        }
    }    

    .ninety-days-banner img, .bucketlist .polaroid-banner img { 
        max-width: 100%; 
    }

    .quote blockquote { 
        font-size: 30px; 
    }

    .banner > div { 
        overflow: hidden;
    }

    .banner, .card {
        flex-direction: column;
    }

    .card { 
        gap: 40px; 
    }

    .compflex .timeline-section { 
        margin: 20px auto;
    }

    .compflex > div:first-child, .compflex > div:last-child, .compflex.twothirds > div:first-child, .compflex.twothirds > div:last-child {
        width: 90%;
    }
}

